/* 
 * colors 
 */
.playPauseButton {
  position: relative;
  left: 0;
  right: 0;
  background-color: #8e5929;
  border: none;
  outline: none;
  height: 8rem;
  width: 8rem;
  font-size: 2rem;
  cursor: not-allowed;
  -moz-border-radius: 8rem;
  -webkit-border-radius: 8rem;
  border-radius: 8rem;
  box-shadow: 0px 4px 35px -10px black;
  transition: transform linear 100ms; }
  .playPauseButton:focus {
    outline: none; }
  .playPauseButton:active {
    transform: scale3d(1.25, 1.5, 1) translateY(1rem); }
  .playPauseButton:before {
    content: "";
    position: absolute;
    top: 2.66667rem;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 2.66667rem;
    height: 2.66667rem;
    border-style: solid;
    border-width: 1.33333rem 0 1.33333rem 2.66667rem;
    border-color: transparent transparent transparent #222a26;
    transition: 250ms ease-in-out; }
  .playPauseButton--active:before {
    border-width: 0 0.88889rem 0 0.88889rem;
    border-color: transparent #222a26 transparent #222a26; }
  .playPauseButton--enabled {
    cursor: pointer;
    background-color: #d8a678; }
