/* 
 * colors 
 */
.container {
  position: sticky;
  top: 0;
  left: 0;
  background-color: #222a26;
  width: 50%;
  height: 100vh;
  padding: 7rem 1rem 4rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center; }

.title {
  margin: 0;
  color: #656960;
  text-align: center;
  margin-bottom: 2rem;
  font-size: 3rem; }

.timer {
  text-align: center;
  color: #d8a678;
  display: flex;
  align-items: center;
  justify-content: center; }

.timerDivider {
  font-size: 6rem; }

.buttonContainer {
  text-align: center;
  margin-top: 4rem; }
  .buttonContainer .stop {
    margin: 1rem 0 0 0;
    text-decoration: underline;
    cursor: not-allowed;
    color: #3f2812; }
    .buttonContainer .stop--active {
      cursor: pointer;
      color: #d8a678; }
