/* 
 * colors 
 */
.container {
  background-color: #f5f5f5;
  width: 50%;
  padding: 7rem 0 4rem 0;
  display: flex;
  flex-direction: column;
  align-items: center; }

.inputContainer {
  display: flex;
  flex-direction: column;
  align-items: center; }

.input {
  padding: 0.75rem;
  border: none;
  outline: none;
  box-shadow: 0 0 20px -8px black;
  height: 2.5rem;
  margin-bottom: 1rem; }

.input {
  width: 25rem;
  cursor: grab; }

.inputHolder {
  position: relative;
  transition: all 150ms ease-in-out; }
  .inputHolder--dragging {
    transform: scale(1.2);
    cursor: grabbing; }
  .inputHolder:before, .inputHolder:after {
    content: "";
    position: absolute;
    right: 0.75rem;
    z-index: 100; }
  .inputHolder:before {
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-bottom: 5px solid #60747b;
    top: calc(20px - 6px); }
  .inputHolder:after {
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid #60747b;
    top: calc(20px + 1px); }
  .inputHolder--active .input {
    background-color: #222a26;
    color: #f5f5f5; }
  .inputHolder--active:before {
    border-bottom-color: #f5f5f5; }
  .inputHolder--active:after {
    border-top-color: #f5f5f5; }

.shuffleButton {
  margin-bottom: 1rem;
  border: none;
  background-color: #60747b;
  color: #f5f5f5;
  cursor: pointer;
  height: 2.5rem;
  padding: 0 1rem; }

.delete {
  position: absolute;
  top: 0.5rem;
  font-size: 1.5rem;
  right: -1.5rem;
  cursor: pointer; }
